// Wysiwyg alignment.
[data-align="right"],
[data-align="center"] {
  display: flex;
  flex-direction: row;
}

[data-align="right"] {
  justify-content: flex-end;
}

[data-align="center"] {
  justify-content: center;
}

.field--name-field-media-oembed-video {
  position: relative;
  overflow: hidden;
  width: 100%;
  padding-top: 56.25%;
}

.media-oembed-content {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
